body {
  background-color: #212121;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  overflow: hidden;
}

.container {
  width: 100%;
  height: 100vh;
  position: relative;
}

#feed-form {
  width: 300px;
  flex-direction: column;
  margin-top: 36px;
  display: flex;
}

#feed-form input, #feed-form textarea {
  height: 54px;
  color: #4b4b4b;
  background: #fff;
  border: none;
  border-radius: 5px;
  margin-bottom: 15px;
  padding: 0 20px;
  font-size: 14px;
  font-weight: 300;
}

#feed-form textarea {
  resize: none;
  min-height: 100px;
  max-width: 300px;
  padding-top: 10px;
  font-size: 16px;
}

.button_submit:hover, .feed-form input:hover {
  transform: scale(1.009);
  box-shadow: 0 0 3px #212529;
}

.button_submit {
  width: 100%;
  height: 54px;
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background: #f26527;
  border: none;
  border-radius: 5px;
  font-size: 14px;
  font-weight: 500;
}

.img {
  display: inline-block;
}

.wrap {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.wrapper {
  justify-content: space-evenly;
  align-items: center;
  margin-bottom: -20px;
  display: flex;
}

.subtitle {
  text-align: center;
  color: #f26527;
  text-transform: uppercase;
  margin: 0 0 0 20px;
  font-size: 30px;
}

@media (prefers-reduced-motion: no-preference) {
  .img {
    animation: logo-spin 20s linear infinite;
  }
}

@keyframes logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

